import WMSCapabilities from 'ol/format/WMSCapabilities';

const URL_GET_MAPS = `http://enplus.petyaogurkin.keenetic.pro/api/maps/`;
const URL_GET = `http://enplus.petyaogurkin.keenetic.pro/api/projects/`
const URL_ADD = 'http://enplus.petyaogurkin.keenetic.pro/api/projects/add'
const URL_EDIT = 'http://enplus.petyaogurkin.keenetic.pro/api/projects/edit/'
const URL_DELETE = 'http://enplus.petyaogurkin.keenetic.pro/api/projects/remove/'
/* */

async function RequestCapabilities(map) {
    const parser = new WMSCapabilities();
    const URL_CAPABILITIES = `https://qgisserver.petyaogurkin.keenetic.pro/ogc/${map}?VERSION=1.3.0&REQUEST=GetCapabilities&SERVICE=WMS`
    const res = await fetch(URL_CAPABILITIES)
    const text = await res.text();
    const result = parser.read(text);
    return result
}

function GetLayers(data) {
    let layers = "";
    for (let i = 0; i < data.Capability.Layer.Layer.length; i++) {
        layers += data.Capability.Layer.Layer[i].Name + ','
    }
    return layers
}

async function GetMaps(token) {
    const res = await fetch(URL_GET_MAPS, { headers: { 'Authorization': `Bearer ${token}` } })
    const receivedMaps = await res.json();
    return receivedMaps;
}

async function Get() {
    const res = await fetch(URL_GET)
    const receivedCatalogs = await res.json();
    return receivedCatalogs;
}

async function Add(project, token) {
    const send = await fetch(URL_ADD, { method: 'POST', body: JSON.stringify(project), headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' } })
    console.log(send)
}

async function Edit(project, token) {
    const send = await fetch(URL_EDIT + `${project._id}`, { method: 'Edit', body: JSON.stringify(project), headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' } })
    console.log(send)
}

async function Delete(project, token) {
    const send = await fetch(URL_DELETE + `${project._id}`, { method: 'DELETE', headers: { 'Authorization': `Bearer ${token}` } })
    console.log(send)
}

export default {
    state: {
        originalProjects: '[]',
        projects: [],
        delProjects: [],
        existingMaps: [],
        selectedProject: null,
        projectsToDisplay: [],
        capabilities: {},
    },

    actions: {
        async Capabilities(ctx, project) {
            const exists = ctx.state.capabilities[project._id];
            if (!exists) {
                RequestCapabilities(project.map).then(data => {
                    const layers = GetLayers(data);
                    ctx.commit("updateCapabilities", { id: project._id, layers: layers })
                })
            }
        },

        async FetchProjects(ctx) {
            const receivedProjects = await Get();
            ctx.commit('updateProjects', receivedProjects);
        },

        async FetchMaps(ctx) {
            const user = JSON.parse(localStorage.getItem("YENISEI_AUTH"));
            const res = await GetMaps(user.accessToken);
            ctx.commit('updateExistingMaps', res);
        },

        UpdateProjectsToDisplay(ctx, project) {
            ctx.commit("updateProjectsToDisplay", project._id)
            this.dispatch("Capabilities", project);
        },

        AddProject(ctx) {
            const project = {
                title: '',
                description: '',
                section: '',
                map: '',
                check: '',
                action: 'add'
            }
            const length = ctx.getters.projectsLength;
            console.log(length)
            ctx.commit('addProject', project);
            ctx.commit('updateSelectedProject', length);
        },

        DeleteProject(ctx, index) {
            ctx.commit('updateSelectedProject', null);
            ctx.commit('deleteProject', index);
        },

        SelectProject(ctx, index) {
            ctx.commit('updateSelectedProject', index);
        },

        SaveProjects(ctx) {
            const user = JSON.parse(localStorage.getItem("YENISEI_AUTH"));
            const catalogs = ctx.state.projects
            const delProjects = ctx.state.delProjects
            catalogs.forEach(p => {
                if (p.action == "add") {
                    Add(p, user.accessToken);
                    p.action = "done";
                }
                if (p.action == "edit") {
                    Edit(p, user.accessToken);
                    p.action = "done"
                }
            })
            delProjects.forEach(n => {
                Delete(n, user.accessToken)
            })
        },

    },
    mutations: {
        updateCapabilities(state, data) {
            state.capabilities[data.id] = data.layers;
        },

        updateProjectsToDisplay(state, id) {
            const project = state.projects.find(p => p._id == id)
            const el = state.projectsToDisplay.findIndex(p => p._id == id)
            if (el == -1) {
                state.projectsToDisplay.push(project)
            }
            else {
                state.projectsToDisplay.splice(el, 1);
            }
        },

        updateExistingMaps(state, maps) {
            state.existingMaps = maps
        },

        addProject(state, project) {
            state.projects.push(project)
        },

        updateSelectedProject(state, index) {
            if (index != null && state.projects[index].action != "add") {
                state.projects[index].action = "edit";
            }
            state.selectedProject = index
        },

        updateProjects(state, projects) {
            state.originalProjects = JSON.stringify(projects)
            state.projects = projects
        },

        deleteProject(state, index) {
            if (state.projects[index].action == "add") {
                state.projects.splice(index, 1);
            }
            else {
                state.delProjects.push(state.projects[index]);
                state.projects.splice(index, 1);
            }
        },
    },

    getters: {
        capabilities(state) {
            return state.capabilities
        },
        projectsToDisplay(state) {
            return state.projectsToDisplay
        },
        existingMaps(state) {
            return state.existingMaps
        },
        projectsLength(state) {
            return state.projects.length
        },
        projects(state) {
            return state.projects
        },
        selectedProject(state) {
            return state.selectedProject
        },
    },
}