<template>
  <transition name="fade" mode="out-in">
    <div
      class="
        message-wrapper
        position-absolute
        top-0
        start-50
        translate-middle-x
        m-1
      "
      v-if="showMessage"
    >
      <div class="card text-center shadow">
        <div class="card-header bg-info">Оповещение</div>
        <div class="card-body">
          <p class="card-text">
            {{ message }}
          </p>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";
export default {
  computed: mapGetters(["message", "showMessage"]),
  methods: mapActions([]),
};
</script>

<style>
.message-wrapper {
  width: 300px;
  z-index: 10;
}
</style>