export default {
    state: {
        message: null,
        showMessage: false,
        displayTime: 3000
    },
    actions: {
        async DisplayMessage(ctx, message) {
            ctx.commit('updateMessage', message);
            ctx.commit('updateShowMessage', true);
            setTimeout(() => { ctx.commit('updateMessage', ""), ctx.commit('updateShowMessage', false); }, ctx.state.displayTime);
        },

        async DisplayMessageEver(ctx, message) {
            ctx.commit('updateMessage', message);
        }
    },
    mutations: {
        updateMessage(state, message) {
            state.message = message

        },
        updateShowMessage(state, show) {
            state.showMessage = show
        }
    },
    getters: {
        message(state) {
            return state.message
        },
        showMessage(state) {
            return state.showMessage
        }
    },
}