import { Encode } from "./base64/code"

const URL_GET = `http://enplus.petyaogurkin.keenetic.pro/api/catalog/`
const URL_ADD = 'http://enplus.petyaogurkin.keenetic.pro/api/catalog/add'
const URL_EDIT = 'http://enplus.petyaogurkin.keenetic.pro/api/catalog/edit/'
const URL_DELETE = 'http://enplus.petyaogurkin.keenetic.pro/api/catalog/remove/'


async function Get() {
    const res = await fetch(URL_GET)
    const receivedCatalogs = await res.json();
    return receivedCatalogs;
}

async function Add(catalog, token) {
    const send = await fetch(URL_ADD, { method: 'POST', body: JSON.stringify(catalog), headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' } })
    console.log(send)
}

async function Edit(catalog, token) {
    const send = await fetch(URL_EDIT + `${catalog._id}`, { method: 'Edit', body: JSON.stringify(catalog), headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' } })
    console.log(send)
}

async function Delete(catalog, token) {
    const send = await fetch(URL_DELETE + `${catalog._id}`, { method: 'DELETE', headers: { 'Authorization': `Bearer ${token}` } })
    console.log(send)
}


export default {
    state: {
        originalCatalogs: '[]',
        catalogs: [],
        delCatalogs: [],
        selectedCatalog: null,
    },
    actions: {
        async FetchCatalogs(ctx) {
            const receivedCatalogs = await Get();
            ctx.commit('updateCatalogs', receivedCatalogs);
        },

        TestIMG(ctx, e) {
            const file = e.target.files
            const catalog = ctx.state.catalogs[ctx.state.selectedCatalog];
            Encode(file, catalog);
        },

        AddCatalog(ctx) {
            const catalog = {
                title: '',
                description: '',
                img: [],
                action: 'add'
            }
            const length = ctx.getters.catalogsLength;
            ctx.commit('addCatalog', catalog);
            ctx.commit('updateSelectedCatalog', length);
        },

        DeleteCatalog(ctx, index) {
            ctx.commit('updateSelectedCatalog', null);
            ctx.commit('deleteCatalog', index);
        },

        SelectCatalog(ctx, index) {
            ctx.commit('updateSelectedCatalog', index);
        },

        SaveCatalogs(ctx) {
            const user = JSON.parse(localStorage.getItem("YENISEI_AUTH"));
            const catalogs = ctx.state.catalogs
            const delCatalogs = ctx.state.delCatalogs
            catalogs.forEach(c => {
                if (c.action == "add") {
                    console.log(123)
                    Add(c, user.accessToken);
                    c.action = "done";
                }
                if (c.action == "edit") {
                    Edit(c, user.accessToken);
                    c.action = "done"
                }
            })
            delCatalogs.forEach(n => {
                Delete(n, user.accessToken)
            })
        },

        CancelCatalogs(ctx) {
            ctx.commit('updateSelectedCatalog', null);
            ctx.commit('cancelCatalogs')
        }
    },
    mutations: {

        cancelCatalogs(state) {
            state.delCatalogs = [];
            state.catalogs = JSON.parse(state.originalCatalogs)
        },

        addCatalog(state, catalog) {
            state.catalogs.push(catalog)
        },

        updateSelectedCatalog(state, index) {
            if (index != null && state.catalogs[index].action != "add") {
                state.catalogs[index].action = "edit";
            }
            state.selectedCatalog = index
        },

        updateCatalogs(state, catalogs) {
            state.originalCatalogs = JSON.stringify(catalogs)
            state.catalogs = catalogs
        },

        deleteCatalog(state, index) {
            if (state.catalogs[index].action == "add") {
                state.catalogs.splice(index, 1);
            }
            else {
                state.delCatalogs.push(state.catalogs[index]);
                state.catalogs.splice(index, 1);
            }
        }

    },
    getters: {
        catalogsLength(state) {
            return state.catalogs.length
        },
        catalogs(state) {
            return state.catalogs
        },
        selectedCatalog(state) {
            return state.selectedCatalog
        },
    },
}